@charset "UTF-8";
.App {
  text-align: center; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px; }

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white; }

.App-title {
  font-size: 1.5em; }

.App-intro {
  font-size: large; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

blockquote .quotation {
  font-size: 16px;
  quotes: "“" "”" "‘" "’";
  line-height: 2;
  border-radius: 3px;
  font-style: italic;
  text-align: center;
  color: #4a4a4a;
  margin: 1rem auto 2rem;
  color: #4a4a4a; }

blockquote .quotation:before {
  content: open-quote;
  display: inline;
  height: 0;
  line-height: 0;
  left: -15px;
  position: relative;
  top: 20px;
  color: #ccc;
  font-size: 3em; }

blockquote .quotation::after {
  content: close-quote;
  display: inline;
  height: 0;
  line-height: 0;
  left: 10px;
  position: relative;
  top: 20px;
  color: #ccc;
  font-size: 3em; }

footer.quote_footer {
  margin: 0;
  text-align: right;
  font-size: 1.5em;
  font-style: italic; }



